<template>
	<div>
		<scroll class="singger-wrapper" :data="newSingers" :listenScroll="true"
			@scroll="scroll" ref="newSingers" :probeType="3">
			<ul class="singer-list" ref="singerList">
				<li v-for="singers in newSingers" class="singer-class">
					<h3 class="singer-class-name">{{singers.title}}</h3>
					<div v-for="item in singers.items" class="singer-item"  @click="selectSinger(item)">
						<div class="singerIcon"><img v-lazy="item.headIcon"></div>
						<div class="singerName">
							<p>{{item.name}}</p>
						</div>
					</div>
				</li>
			</ul>
			<div class="singerFloor">
				<ul @touchmove.stop.prevent="onTouchMove($event)">
					<li v-for="(singers,index) in newSingers" class="singerFloorItem" :class="{actived: index===currentIndex}" @touchstart.stop.prevent="skipSingerClass(index,$event)">{{singers.title.slice(0,1)}}</li>
				</ul>
			</div>
			<div v-show="!newSingers.length" class="loading-container">
	      <loading></loading>
	    </div>
	    <div class="fixed-title" ref="singerTitle" v-show="scrollY>0">{{singerTitle}}</div>
		</scroll>
    </div>
</template>

<script type="text/javascript">
	import Scroll from 'base/scroll/scroll'
	import {getSingerList} from 'api/singer'
	import {ERR_OK} from 'api/config'
	import Loading from 'base/loading/loading'
  import { playlistMixin } from 'common/js/mixin'
	const HOT_NAME = '热门'
	const HOT_SINGER_LEN = 10
	export default {
    mixins: [playlistMixin],
		data() {
			return {
				Singers: [],
				newSingers: [],
				listHeight: [],
				scrollY: 0,
        currentIndex: 0,
        diff: -1
			}
		},
		components: {
			Scroll,
			Loading
		},
		methods: {
			_getSingerList () {
				getSingerList().then((res) => {
					if (res.code === ERR_OK) {
            this.Singers = res.data.list
            this.newSingers = this._normalLizeSinger(this.Singers)
				  }
			  })
		  },
			_normalLizeSinger (list) {
		        let map = {
		        	hot: {
		        		title: HOT_NAME,
		        		items: []
		        	}
		        }
		        list.forEach((item, index) => {
		        	if (index < HOT_SINGER_LEN) {
                console.log(item)
		        		map.hot.items.push({
		        			id: item.Fsinger_mid,
		        			name: item.Fsinger_name,
		        			headIcon: 'http://y.gtimg.cn/music/photo_new/T001R300x300M000' + item.Fsinger_mid + '.jpg?max_age=2592000' 
		        		})
		        	}
		        	const key = item.Findex
		        	if (!map[key]) {
		        		map[key] = {
		        			title: key,
		        			items: []
		        		}
		        	}
		    		map[key].items.push({
		    			id: item.Fsinger_mid,
		    			name: item.Fsinger_name,
		    			headIcon: 'http://y.gtimg.cn/music/photo_new/T001R150x150M000' + item.Fsinger_mid + '.jpg?max_age=2592000'
		    		})
	            })
		        // 为了得到有序列表，我们需要处理map
		        let hot = []
		        let ret = []
		        for (let key in map) {
		        	let val = map[key]
		        	if (val.title === HOT_NAME) {
		        		hot.push(val)
		        	}else if (val.title.match(/[a-zA-Z]/)) {
		        	   ret.push(val)
		            }
		        }

		        ret.sort((a, b) => {
		        	return a.title.charCodeAt(0) - b.title.charCodeAt(0)
		        })
		        return hot.concat(ret)
			},
					// 收集高度
	    _calculateHeight () {
        let height = 0
        let singerList = document.getElementsByClassName('singer-class')
        this.listHeight.push(height)
        for(let i = 0; i < singerList.length; i++) {
         	let item = singerList[i]
         	height += item.clientHeight
         	this.listHeight.push(height)
        }
	    },
			// 获取滚动位置
			scroll (pos) {
        // console.log(this.scrollY)
				this.scrollY = -pos.y
				this.currentIndex = this._currentIndex()
			},
			// 获取滚动至了哪一个区间
			_currentIndex () {
				if (this.scrollY <= 0) {
					return 0
				}
        for (var i = 0; i < this.listHeight.length; i++) {
        	let height_1 = this.listHeight[i]
        	let height_2 = this.listHeight[i+1]
        	this.diff = height_2 - this.scrollY
        	if (this.scrollY >= height_1 && this.scrollY < height_2) {
        		return i
        	}
        }
        // 当滚动到底部，且-newY大于最后一个元素的上限
        return this.listHeight.length - 2
			},
      skipSingerClass (index, $event) {
        this._scrollTo(index)
        this.touch.y1 = $event.changedTouches[0].pageY
        this.touch.index = index
        this.currentIndex = index
      },
      onTouchMove ($event) {
      	this.touch.y2 = $event.changedTouches[0].pageY
      	let delta = (this.touch.y2 - this.touch.y1)/20
      	this.currentIndex = Math.floor(delta + this.touch.index)
      	if (this.currentIndex > 22) {
      		this.currentIndex = 22
      	}
      	if (this.currentIndex < 0) {
      		this.currentIndex = 0
      	}
      	this._scrollTo(this.currentIndex)
      },
      _scrollTo (index) {
      	let singerList = document.getElementsByClassName('singer-class')
        let el = singerList[index]
        this.$refs.newSingers.scrollToElement(el,400)
      },
      selectSinger (singer) {
      	this.$emit("selectSinger",singer)
      },
      handlePlayList(playList) {
        if (playList.length) {
          this.$refs.newSingers.$el.style.bottom = '60px'
          this.$refs.newSingers.refresh()
        }
      }
		},
		created () {
			this._getSingerList()
			setTimeout(() => {
        this._calculateHeight()
			},500)
			this.touch = {
				y1: null,
				y2: null,
				index: null
			}
		},
		computed: {
			singerTitle() {
				return this.newSingers[this.currentIndex] ? this.newSingers[this.currentIndex].title : ''
			}
		},
		watch: {
			diff (newVal) {
				let fixedTop = (newVal > 0 && newVal < 30) ? (newVal - 30 + 'px') : 0
				if (this.fixedTop === fixedTop) {
          return
        }
        this.fixedTop = fixedTop
          this.$refs.singerTitle.style.transform = 'translate3d(0,'+fixedTop+',0)'
			}
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  @import "../../common/stylus/mixin"
  .singger-wrapper{
  	position: fixed
  	top: 88px
  	bottom: 0
  	width: 100%
    overflow: hidden
  }
  .singer-class-name{
  	line-height: 30px
  	background: $color-highlight-background
  	color: $color-text-l
  	font-size: $font-size-medium
  	padding: 0 20px
  	margin-bottom: 20px
  }
  .singer-item
  	display: flex
  	height: 50px
  	padding-bottom: 20px
  	margin-left: 30px
  	.singerIcon
  		width: 50px
  		padding-right: 20px
  		height: 50px
  		img
  			width: 100%
  			height: 50px
  			border-radius: 25px
  	.singerName
  		padding-right: 10px
  		flex: 1
  		height: 50px
  		line-height: 50px
  	    color: $color-text-l
  	    no-wrap()
  .singerFloor
  	position: absolute
  	right: 0
  	top: 50%
  	width: 20px
  	font-size: 14px
  	transform: translate(0,-50%)
  	text-align: center
  	background: $color-background-d
  	border-radius: 20px
  	padding: 10px 0
  	.singerFloorItem
  		color: $color-text-l
  		width: 20px
  		padding: 3px 0
      .singerFloorItem.actived{
          color: orange
      }
  .loading-container{
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
    }
   .fixed-title{
   	position: absolute
   	width: 100%
   	top: 0
   	height: 30px
   	left: 0
   	line-height: 30px
  	background: $color-highlight-background
  	color: $color-text-l
  	font-size: $font-size-medium
  	padding: 0 20px
  	margin-bottom: 20px
   }
</style>


