<template>
	<transition name="slider">
    <music-list :songs="newSingerSonges" :bgurl="singer.headIcon" :title="title"></music-list>
  </transition>
</template>

<script type="text/javascript">
  import { getSingerDetail} from 'api/singer'
  import { getMusic} from 'api/song'
  import { ERR_OK } from 'api/config'
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import { creatSong, processSongsUrl} from 'common/js/song'
  import musicList from '../music-list/musicList'
	export default {
    computed: {
      ...mapGetters(['singer']),
      title() {
        console.log(this.singer)
        return this.singer.name
      }
    },
    components: {
      musicList
    },
    data () {
      return {
        singerSonges: [],
        newSingerSonges: [],
      }
    },
    methods: {
      _normalLizeSong(musicData) {
        let songItem = creatSong(musicData)
        this.newSingerSonges.push(songItem)
      }
    },
    created () {
      getSingerDetail(this.singer.id).then((res) => {
        if (res.code === ERR_OK) {
          this.singerSonges = res.data.list
          this.singerSonges.forEach((item) => {
            this._normalLizeSong(item.musicData)
          })
          processSongsUrl(this.newSingerSonges)
        }
      })
    },
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  .slider-enter-active,.slider-leave-active{
    transition: all .3s
  }
  .slider-enter,.slider-leave-to{
    transform: translate3d(100%, 0, 0)
  }
</style>


