<template>
  <div class="search-result-wrapper">
    <ul class="singer-list">
      <li class="item" v-show="isZhidao" @click="select">
        <i class="icon-mine icon"></i>
        <span class="text">{{zhida.singername}}</span>
      </li>
    </ul>
    <ul class="song-list">
      <li class="item" v-for="item in songList" @click="playMusic(item)">
        <i class="icon-music icon"></i>
        <span class="text" v-html="item.songname+'-'+item.singer[0].name"></span>
      </li>
    </ul>
  </div>
</template>

<script type="text/javascript">
  import { getSearchList } from 'api/search'
  import { ERR_OK } from 'api/config'
  import { mapActions } from 'vuex'
  import { creatSong, processSongsUrl} from 'common/js/song'
  export default {
    props: {
      query: {
        default: ''
      }
    },
    data() {
      return {
        isZhidao: false,
        zhida: {},
        songList: []
      }
    },
    methods: {
      ...mapActions(['insertSong']),
      search(query) {
        getSearchList(query).then(res=>{
          if (res.data.code === ERR_OK) {
            let searchResult = res.data.data
            if (searchResult.zhida.type > 0) {
              this.isZhidao = true
              this.zhida = searchResult.zhida
            }else{
              this.isZhidao = false
            }
            this.songList = searchResult.song.list
          }
        })
      },
      select() {
        // 设置歌手的信息
        let singer = {
          id: this.zhida.singermid,
          name: this.zhida.singername,
          headIcon: 'http://y.gtimg.cn/music/photo_new/T001R300x300M000' + this.zhida.singermid + '.jpg?max_age=2592000'
        }
        console.log(singer)
        this.$emit("selectSinger",singer)
      },
      creatSongItem(item) {
        let songs = []
        songs.push(creatSong(item))
        processSongsUrl(songs)
        return songs[0]
      },
      playMusic(item){
        item = this.creatSongItem(item)
        console.log(item)
        this.insertSong(item)
      }
    },
    watch: {
      query(newVal) {
        this.search(newVal)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .singer-list{
    box-sizing: border-box
  }
  .item{
    display: flex
    color: $color-text-d
    padding: 10px 0
    line-height: 20px
  }
  .icon{
    width: 35px
    text-align: left
    font-size: 14px
    line-height: 20px
  }
  .text{
    flex: 1
    font-size: 18px
    no-wrap()
  }
</style>



