<template>
	<div class="search">
		<div class="search-box">
			<search-box @query="queryChange"></search-box>
		</div>
		<scroll ref="shortcutWrapper" class="shortcut-wrapper" :data="hotKey">
			<div class="shortcutBox">
        <div class="shortcut" v-show="!query.length">
          <div class="hot-key">
            <h2 class="hot-title">热门搜索</h2>
            <ul>
              <li class="hot-key-item" v-for="item in hotKey" @click="queryChange(item.k)">
                {{item.k}}
              </li>
            </ul>
          </div>
          <div class="search-history">
            <h2 class="history-title">
              <span class="text">搜索历史</span>
              <span class="clear">
                <i class="icon-clear"></i>
              </span>
            </h2>
            <search-list :searches="searchHistory" @select="queryChange" @delete="deleteHistory"></search-list>
          </div>
        </div>
        <div class="search-result" v-show="query.length">
          <search-result :query="query" @selectSinger="selectSinger" :searches="searchHistory"></search-result>
        </div>   
      </div>
    </scroll>
    <router-view></router-view>
	</div>
</template>

<script type="text/javascript">
	import SearchBox from 'base/search-box/search-box'
  import { ERR_OK } from 'api/config'
  import {getSearchList, getHotKey} from 'api/search'
  import SearchResult from '../../components/search-result/search-result'
  import SearchList from 'base/search-list/search-list'
  import Scroll from 'base/scroll/scroll'
  import {mapGetters, mapMutations, mapActions} from 'vuex'
  import {playlistMixin, searchMixin} from 'common/js/mixin'
	export default {
    mixins: [playlistMixin],
		name: 'search',
		components: {
			SearchResult,
			SearchBox,
      Scroll,
      SearchList
		},
		data() {
			return {
				hotKey: [],
				query: '',
			}
		},
    computed: {
      ...mapGetters(['searchHistory'])
    },
		methods: {
      ...mapMutations(['SET_SINGER']),
      ...mapActions(['saveSearchHistory', 'deleteSearchHistory']),
      handlePlayList(playList) {
        if (playList.length) {
          this.$refs.shortcutWrapper.$el.style.bottom = '60px'
          this.$refs.shortcutWrapper.refresh()
        }
      },
      queryChange(query) {
      	if (query) {
          this.saveSearchHistory(query)
        }
        this.query = query
      },
      selectSinger(singer) {
        this.$router.push({
          path: `/search/${singer.id}`
        })
        this.SET_SINGER(singer)
      },
      deleteHistory(query) {
        this.deleteSearchHistory(query)
      }
		},
		created() {
			//获取热门搜索
			getHotKey().then(res => {
				if (res.code === ERR_OK) {
          this.hotKey = res.data.hotkey.slice(0, 10)
        }
			})
		},
		watch: {
			query(newVal) {
         
			}
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .search, search-box{
    margin: 20px
  }
  .shortcut-wrapper{
  	position: fixed
    top: 178px
    left: 0
    bottom: 0
    width: 100%
    overflow: hidden
  }
  .shortcut{
  	height: 100%
    overflow: hidden
  }
  .search-history,.hot-key{
    margin: 0 20px 20px 20px
  }
  .history-title, .hot-title{
  	font-size: $font-size-medium
    color: $color-text-l
    margin-bottom: 20px
  }
  .hot-key-item{
  	display: inline-block
    padding: 5px 10px
    margin: 0 20px 10px 0
    border-radius: 6px
    background: $color-highlight-background
    font-size: $font-size-medium
    color: $color-text-d
  }
  .clear{
  	font-size: $font-size-medium
    color: $color-text-d
    float: right
  }
  
  .search-result
      // position: fixed
      // width: 100%
      // top: 178px
      // left: 0
      // bottom: 0
      padding: 0 30px
      box-sizing: border-box
</style>


